import React,{useState, useEffect, useRef} from 'react'
import {Form} from 'react-bootstrap'
import ElemDeleteControl from "./ElemDeleteControlWrapper"
import styles2 from '../css/COLUMN.module.css'
import CriteriaWidget from './ImageCarouselComponent/index.js';
import QuickTools from '../../ReusableComponents/QuickTools/QuickTools';
import { getSuggestedId } from "../../components/IFormDesignerLayout/Logic/UserIdLogic";
import { handleDisplayProperty } from '../../components/IFormDesignerLayout/IFormDesignerLogic'
import { GetState, GetStateFromData } from '../../components/CustomHooks/CustomHooks';

function IMAGECAROUSEL(props) {
    const contexts = GetStateFromData(props.containerId);
  
  const {controlSelected,tempCanWidth} = GetState("controlSelected","tempCanWidth");
    
  const isSubformInPage = props?.isSubformInPage || false;

      const {IdPrecedence, deleteControl , formState2, device,imageBaseURL, openQuickToolFor }=contexts.contexts;
      const conditionalQuickToolOpening =
    props.id === openQuickToolFor ? true : false

  
    const data = props.data; 
    const actualControlId = props.data.id;
    let mappingId = props.data["style"] && props.data["style"]["MappingId"];
    let userDefinedId = props.data["style"] && props.data["style"]["CustomId"];
    
    let suggestedId = getSuggestedId(actualControlId, mappingId, userDefinedId, IdPrecedence);
    const [open, setOpen] =  useState(conditionalQuickToolOpening);
    const [mobileDisplay, setMobileDisplay] = useState(true);
    const [tabDisplay, setTabDisplay] = useState(true);
    
    const startMobile = useRef();
    const endMobile= useRef();
    const startTab= useRef();
    const endTab= useRef();
    useEffect(() => {
        if (formState2 && formState2.style) {
            startMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]);
            endMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]);
            startTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]);
            endTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]);
        }
    },[formState2.style?.["Breakpoint"], formState2.style?.["Breakpoint"]?.["Mobile"], formState2.style?.["Breakpoint"]?.["Tab"]]); 

    useEffect(() => {
        if(props?.previewmode)
        handleDisplayProperty(props, device, setMobileDisplay, setTabDisplay, startMobile.current, endMobile.current, startTab.current, endTab.current)
      },[props?.data?.style?.["MobileVisible"], props?.data?.style?.["TabsVisible"], device, endMobile, endTab, startMobile, startTab,tempCanWidth]);


    function handleImageClick() {
        let elem = document.getElementById(data.id+"_menu");
        elem.focus();
    }
    useEffect(() => {
        if (props?.previewmode && document.getElementById(`${suggestedId}_group`)) {
          document.getElementById(`${suggestedId}_group`).ondragstart = () => {
            return false;
          };
        }
      }, [props?.previewmode])
    
    var cData=[];
    if(props.data && props.data.style){
        if(!Object.keys(props.data.style).includes("imageList")){
            cData.push(<div id="emptyDiv1" className={styles2.imgCarouselParent}></div>);
        }
    }
    if(props.data && props.data.style && props.data.style["imageList"]){
        if(props.data.style["imageList"].length<1){
            cData.push(<div id="emptyDiv2" className={styles2.imgCarouselParent}></div>)
        }
        props.data.style["imageList"].map((imageDetails,index)=>{ 

            let sectionBodyStyle2 = {
                display: "block",
                marginLeft: "auto",
                marginRight: "auto",
                objectPosition: "top",
                width: imageDetails.imageStyle?.resize === "fullWidth" ? "100%" : imageDetails.imageStyle?.widthType==="px" ? imageDetails.imageStyle?.width ? imageDetails.imageStyle?.width + imageDetails.imageStyle?.widthType : "100%" :  imageDetails.imageStyle?.size ? imageDetails.imageStyle?.size + imageDetails.imageStyle?.widthType : "100%" ,
                height: imageDetails.imageStyle?.resize === "fullWidth" ? "100%" : imageDetails.imageStyle?.widthType==="px" ? imageDetails.imageStyle?.height ? imageDetails.imageStyle?.height + imageDetails.imageStyle?.widthType : "100%" : imageDetails.imageStyle?.size ? imageDetails.imageStyle?.size + imageDetails.imageStyle?.widthType : "100%",
                transform: imageDetails.imageStyle && imageDetails.imageStyle.transform, 
                aspectRatio : imageDetails.imageStyle &&  imageDetails.imageStyle.aspectRatio,
            };
            cData.push (
                <>
                <div
                    key={index}
                    style={{ 
                        height: data?.style?.["ImageCarouselHeight"] ?  
                        data.style["ImageCarouselHeight"] + 'px' :
                        "220px",
                        width: "100%",
                    }}
                >                
                    <img 
                    control_id={props.data.id}
                        id={"image_"+index} 
                        key={index}
                        src={`${imageBaseURL}${imageDetails?.imageFile}`} 
                        style={sectionBodyStyle2}
                        alt='Img File Icon'
                    />
                </div>
                </>
            );
        })
    }
  
    
    var padding_left = props.data.style && props.data.style["SpacingLeft"];
    if(padding_left !==undefined && padding_left !==null && padding_left !==''){

    } else {
        padding_left="0px";
    }

    var padding_right = props.data.style && props.data.style["SpacingRight"];
    if(padding_right !==undefined && padding_right !==null && padding_right !==''){

    } else {
        padding_right="0px";
    }

    var padding_top = props.data.style && props.data.style["SpacingTop"];
    if(padding_top !==undefined && padding_top !==null && padding_top !==''){

    } else {
        padding_top="0px";
    }

    var padding_bottom = props.data.style && props.data.style["SpacingBottom"];
    if(padding_bottom !==undefined && padding_bottom !==null && padding_bottom !==''){

    } else {
        padding_bottom="0px";
    }


    var inputStylePadding={  
        paddingLeft : padding_left,
        paddingRight : padding_right,
        paddingTop : padding_top,
        paddingBottom : padding_bottom,
        display: "block",
        position: "relative"
    }



    if(props.previewmode ){
        return (
            <>
            {props.data && props.data.style && props.data.style["Visible"]==="true" ?            
            <Form.Group  id={suggestedId+'_group'}
             className={ [styles2.formStyle, 'spacingThemeControls', styles2.noFocusIC ].join(" ")} key={data.id} 
             style={mobileDisplay === false || tabDisplay === false ? {display:"none"} : inputStylePadding}
             control_id={suggestedId+'_group'}
             tabIndex="1" elem_type='imagecarousel' frameindex={data.frameIndex}  elementindex={data.elementIndex} >
                
                <CriteriaWidget 
                    id={suggestedId}
                    key={data.id} 
                    data={cData} 
                    control_id={suggestedId}
                    elemType={props.data?.elem_type}
                    containerId={props.containerId}
                    height={data && data.style && data.style["ImageCarouselHeight"] ? parseInt(data.style["ImageCarouselHeight"]): 200} 
                    autoplaySpeed={data && data.style && data.style["AutoplaySpeed"] ? parseInt(data.style["AutoplaySpeed"]): 3000}  
                    speed={data && data.style && data.style["Speed"] ? parseInt(data.style["Speed"]): 400} 
                    autoplay={data && data.style && data.style["AllowAutoScroll"] ? data.style["AllowAutoScroll"] === "true" ? true : false: true}
                />
            </Form.Group>
             : null}
            </>
            
       )
    } else if(props.data ){
        return (
            <>
            <div
                style={inputStylePadding}
                tabIndex="1" 
                id={data.id+"_menu"}
                onKeyDown={(e) => props.handleSelected(e)}
            >
                <Form.Group  
                    id={suggestedId+'_group'}
                    className={ [styles2.formStyle, 'spacingThemeControls' ].join(" ")} key={data.id} 
                    control_id={data.id} elem_type='imagecarousel' frameindex={data.frameIndex}  elementindex={data.elementIndex} 
                    onKeyDown={(e) => props.handleSelected(e)}
                >
                    <CriteriaWidget 
                        id={suggestedId}
                        controlId={props.id}
                        elemType={props.data?.elem_type}
                        containerId={props.containerId}
                        data={cData} 
                        key={data.id + "_" + data.style["AllowAutoScroll"]} 
                        height={data && data.style && data.style["ImageCarouselHeight"] ? parseInt(data.style["ImageCarouselHeight"]): 200} 
                        autoplaySpeed={data && data.style && data.style["AutoplaySpeed"] ? parseInt(data.style["AutoplaySpeed"]): 4000}  
                        speed={data && data.style && data.style["Speed"] ? parseInt(data.style["Speed"]): 400} 
                        autoplay={data && data.style && data.style["AllowAutoScroll"] ? data.style["AllowAutoScroll"] === "true" ? true : false: true}
                        handleSelected={isSubformInPage ? {} : (e,id)=>props.handleSelected(e,id,"n")}
                        handleImageClick={()=>handleImageClick()}
                    />
                </Form.Group>
                {controlSelected?.id === props?.id &&
                <ElemDeleteControl 
                    isSubformInPage={isSubformInPage}
                    containerId={props.containerId}
                    id={props.data.id} 
                    data={props.data}
                    deleteControl={deleteControl} 
                    elemType={props.data?.elem_type}
                    setDragging={props.setDragging}
                    handleReorderStart={props.handleReorderStart}
                    handleReorderDrop={props.handleReorderDrop} 
                    dragId={props.dragId}
                    dragType={'column'}
                /> 
    }
                {
                    window?.openQuickTools && open &&  
                    <QuickTools containerId={props.containerId} selectedControl={props.data} open={open} setOpen={setOpen} />
                }
            </div>
            </>
       )
    }else{
        return(<></>);
    }

}


export default IMAGECAROUSEL ;