import React from "react";
import Carousell from "./Carousel.js";
import { makeStyles } from '@material-ui/core/styles';
import { useMediaQuery } from '@material-ui/core';
import { GetStateFromData } from "../../../components/CustomHooks/CustomHooks.js";


const useStyles = makeStyles({
    widget: {
        //border: "1px solid green"
    }
});


const CriteriaWidget = (props) => {
    const classes = useStyles();

    const data = props.data;
    const settings = {
        dots: true,
        speed: props.speed,
        slidesToShow: 1,
        slidesToScroll: 1,
		rtl:false,
        arrows: false,
        infinite: true,
        autoplay: props.autoplay,
        autoplaySpeed: props.autoplaySpeed,
        pauseOnDotsHover: true,
        pauseOnFocus: true,
        height: props.height
    };
    return <>
    
        <div
            className={classes.widget} 
            controlId={props.controlId} 
            style={{ 
                height: props.height, 
                width: props.elemType === "imagecarousel" ?"100%"  : '100%'
            }} 
            key={props.key} id="widget"
        >
            <Carousell
                {...settings}
                data={data}
                controlId={props.controlId}
                secHeight={props?.secHeight}
                handleSelected={(e)=>props.handleSelected(e,props.controlId,"n")}
                handleImageClick={()=>props.handleImageClick()}
            />
        </div>
    </>

}

export default CriteriaWidget;