import React, {useState, useEffect, useRef } from 'react';
import { CardContent } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import ArrowBackIosNewRoundedIcon from '@material-ui/icons/ArrowBackIosRounded';
import ArrowForwardIosRoundedIcon from '@material-ui/icons/ArrowForwardIosRounded'
import Slider from "react-slick";
import { makeStyles } from '@material-ui/core/styles';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

let TILE_MARGIN_TOP = 0;
let TILE_MARGIN_BOTTOM = 10;
let STEPPER_HEIGHT = 11;
let STEPPER_MARGIN_TOP = 10;
let STEPPER_MARGIN_BOTTOM = 10;
let TILE_MARGIN_LEFT = 0;
let TILE_MARGIN_RIGHT = 0;

const sm = 576;
const md = 768; // width <= md, only one slide will be visible in carousel at a time
const lg = 992;
const xl = 1200; // width <= xl, only two slides will be visible in carousel at a time
const xxl = 1400; // width > xl, only three slides will be visible in carousel at a time

// You can customise breackpoint explicitly as defined below In order to change the number of slides as per the available screen width


const TILE_HEIGHT = TILE_MARGIN_TOP + STEPPER_HEIGHT + STEPPER_MARGIN_TOP + STEPPER_MARGIN_BOTTOM;

const useStyles = makeStyles((theme) => ({
    card_size: {
        marginLeft: (rtl) => (rtl ? TILE_MARGIN_RIGHT + "px" : TILE_MARGIN_LEFT + "px"),
        marginRight: (rtl) => (rtl ? TILE_MARGIN_LEFT + "px" : TILE_MARGIN_RIGHT + "px"),
        marginTop: TILE_MARGIN_TOP + "px",
        marginBottom: "0px",
        paddingBottom: "0px",
        border: "1px solid #E4E4E4",
        background: "#FFFFFF 0% 0% no-repeat padding-box",
        boxShadow: "1px 2px 4px 1px #00000029",
        opacity: "1"
    },
    ltThan3: {
        '& .slides': {
            marginLeft: "0px",
            marginRight: "0px",
            direction: "initial"
        },
        '& .slick-dots': {
            color: "#CECECE !important",
            bottom: "8px !important"
        },
        '& .slick-dots li button': {
            width:'0px',
            color:'#0072C6',
            "font-weight": 600,
        },
        '& .slick-dots li button:focus:before': {
            opacity: "0.25 !important",
            color : "#0072C6"
        },
        '& .slick-dots li.slick-active button:before': {
            color:'#0072C6'
        },
        '& .slick-dots li button:before': {
            color:'#FFFFFF',
            fontSize : '10px',
            lineHeight: '10px'
        }
    },
    gtThan3: {
        marginLeft: "0px",
        marginRight: "0px",
        direction: "initial",
        height: "100%",

        '& .slick-next': {
            right: "-20px !important"
        },
        '& .slick-prev': {
            left: "-20px !important"
        },
        '& .slick-dots': {
            color: "#CECECE !important",
            bottom: "8px !important"
        },
        '& .slick-next::before': {
            color: "grey !important"
        },
        '& .slick-prev::before': {
            color: "grey !important"
        },
        '& .slick-dots li button': {
            width:'0px',
            color: "#FFFFFF"
        },
        '& .slick-dots li button:focus:before': {
            opacity: "0.25 !important",
            color : "#0072C6"
        },
        '& .slick-dots li.slick-active button:focus:before,.slick-dots li button:hover:before ': {
            opacity: "1 !important"
        },
        '& .slick-dots li.slick-active button:before': {
            color:'#0072C6',

        },
        '& .slick-dots li button:before': {
            color:'#FFFFFF',
            fontSize : '10px',
            lineHeight: '10px'
        }

    },
    cardcontent: {
        paddingLeft: 0,
        paddingRight: 0,
        paddingTop: 0,
        "&:last-child": {
            paddingBottom: 0
        }
    },
    
    '.slider-container': {
        maxWidth: "100vw",
        width: "100%",
        overflow: "hidden"
    }
}));

const globalBreakpoints_3 = {
    responsive: [
        {
            breakpoint: xxl,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                infinite: true,
                dots: true,
            }
        },
        {
            breakpoint: xl,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                infinite: true,
                dots: true,
            }
        },
        {
            breakpoint: md,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                infinite: true,
                dots: true,
            }
        }
    ]
};

const globalBreakpoints_1 = {
    responsive: [
        {
            breakpoint: xxl,
            settings: {
                slidesToShow: 1,
                infinite: false,
            }
        }
    ]
};

const globalBreakpoints_2 = {
    responsive: [
        {
            breakpoint: xxl,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                infinite: true,
                dots: true,
            }
        }
    ]
};


const Carousell = (props) => {
    const [sliderActive, setSliderActive] = useState(false);
    const [currentHeight, setCurrentHeight] = useState("");
    const [windowWidth, setWindowWidth] = useState(false);
    const classes = useStyles(props.rtl);
    let rowHeight = null;
    const slider = useRef(null)

    useEffect(() => {
        const resizeWidth = window.addEventListener("resize", () => {
            if ((props.data.length === 3 && window.innerWidth <= "1200") || (props.data.length === 2 && window.innerWidth <= "768")) {
                setWindowWidth(true);
            }
            else {
                setWindowWidth(false);
            }
        });
        return () => {
            window.removeEventListener("resize", resizeWidth);
        };
    }, []);

    useEffect(() => {
        if (props.data.length === 1 || props.data.length === 2 || props.data.length === 3) {
            if ((props.data.length === 3 && windowWidth === true) || (props.data.length === 2 && windowWidth === true)) {
                setSliderActive(false)
                rowHeight = (parseInt(props.height) - TILE_HEIGHT) + "px";
                setCurrentHeight(rowHeight);
            }
            else {
                setSliderActive(true)
                rowHeight = (parseInt(props.height) - (TILE_MARGIN_TOP + TILE_MARGIN_BOTTOM)) + "px";
                setCurrentHeight(rowHeight);
                setWindowWidth(false)
            }

        }
        else {
            setSliderActive(false)
            rowHeight = (parseInt(props.height) - TILE_HEIGHT) + "px";
            setCurrentHeight(rowHeight);
        }
    })



    const currentResponsive = () => {
        if (props.data.length === 0) {
            return { ...Object.assign({ ...props, ...globalBreakpoints_1 }) }
        }
        else if (props.data.length === 2) {
            return { ...Object.assign({ ...props, ...globalBreakpoints_2 }) }
        }
        else if (props.data.length === 3) {
            return { ...Object.assign({ ...props, ...globalBreakpoints_3 }) }
        }
        else {
            return { ...Object.assign({ ...props, ...globalBreakpoints_3 }) }
        }
    }

 

    return (
        <>
            <div style={{direction: "initial"}} className={sliderActive ? classes.ltThan3 : classes.gtThan3} >
                <Slider
                    className={classes['.slider-container']}
                    autoplaySpeed={props?.autoplaySpeed}
                    speed={props?.speed}
                    arrows
                    infinite
                    ref={slider}
                    {...currentResponsive()}>
                    {
                        props?.data?.map((item) => {
                            return (
                                <div>
                                    <Card 
                                        className={classes.card_size} 
                                        style={{ height: currentHeight }} 
                                        control_id={props.controlId} 
                                        onClick={()=>props.handleImageClick()}
                                    >
                                        <CardContent 
                                            className={classes.cardcontent}
                                            control_id={props.controlId}
                                        >
                                            <div 
                                                control_id={props.controlId} 
                                                style={{width:'100%', height: `${props?.secHeight}px`}} 
                                            >
                                                {item}
                                            </div>
                                        </CardContent>
                                    </Card>
                                </div>
                            )
                        })
                    }
                </Slider>
                <span
                style={{
                    top: `${props?.height - (props?.height/2) - 20}px`,
                    position: "absolute",
                    background: "transparent",
                    border: "none"
                }}
                onClick={() => slider?.current?.slickPrev()}
                >
                    <ArrowBackIosNewRoundedIcon />
                </span>
                <span
                style={{ 
                    position: "absolute", 
                    top: `${props.height - (props?.height/2) - 20}px`, 
                    right: "0px",
                    background: "transparent",
                    border: "none"
                 }}
                onClick={() => slider?.current?.slickNext()}
                >
                    <ArrowForwardIosRoundedIcon />
                </span>
            </div>
        </>
    );
}

export default Carousell;
